[class*="__title"] {
  margin-bottom: 3rem;
  h1 {
    font-size: 4rem;
    margin-bottom: 1rem;
    text-transform: capitalize;
    font-weight: 400;

    @media (max-width: 600px) {
      font-size: 3rem;
    }
  }
  span {
    background-color: var(--green);
    height: 5px;
    display: block;
    margin-bottom: 8px;
    border-radius: 50px;

    &:nth-child(2) {
      width: 100px;
    }

    &:last-child {
      width: 50px;
    }
  }
}

.section {
  width: 90%;
  margin: 0 auto;
  min-height: 100vh;
  animation: sectionChange 1s ease-in-out;

  @media (max-width: 900px) {
    padding-left: 20px;
  }
}

.section-wrapper {
  padding: 5rem 0;
}

p {
  font-family: "Nunito", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--white) !important;
}

a {
  text-decoration: none;
  color: var(--white-light-1);
}
